@tailwind base;
@tailwind components;
@tailwind utilities;


*{
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@import url('https://fonts.google.com/specimen/Work+Sans');

@font-face {
  font-family: 'WorkSansBold';
  src: url('./assets/fonts/static/WorkSans-Bold.ttf') format('truetype');
}

@font-face {
  font-family: 'WorkSansRegular';
  src: url('./assets/fonts/static/WorkSans-Regular.ttf') format('truettype');
}

@font-face {
  font-family: 'WorkSansSemiBold';
  src: url('./assets/fonts/static/WorkSans-SemiBold.ttf') format('truettype');
}

@font-face {
  font-family: 'WorkSansSemiVariable';
  src: url('./assets/fonts/WorkSans-VariableFont_wght.ttf') format('truettype');
}